import { useState, useEffect } from "react";

import "./index.css";

const { fetchQuote } = require('./fetchQuote');

function App() {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [targetDates, setTargetDates] = useState([
    {
      date: new Date("01/16/2024"),
      label: "JEE (M) 2024, Ist"
    },
    {
      date: new Date("04/08/2024"),
      label: "JEE (M) 2024, IInd"
    },
    {
      date: new Date("06/04/2024"),
      label: "IIT-JEE Adv, 2024"
    }
  ]);
  const [remainingTime, setRemainingTime] = useState([]);
  const [responseQuote, setResponseQuote] = useState("");
  const [customQuotes, setCustomQuotes] = useState([]);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentDate(new Date());
    }, 1000);
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    async function getQuote() {
      const response = await fetchQuote();
      setResponseQuote(response);
    }
    getQuote();
  }, []);

  const getNewQuote = async () => {
    const response = await fetchQuote();
    setResponseQuote(response);
  }

  const readCustomQuotes = async (event) => {
    const file = event.target.files[0];
    const text = await file.text();
    const quotes = text.split("\n").filter((quote) => quote.trim() !== "");
    setCustomQuotes(quotes);
  };

  const getRandomCustomQuote = () => {
    const index = Math.floor(Math.random() * customQuotes.length);
    setResponseQuote(customQuotes[index]);
  };

  useEffect(() => {
    const remaining = targetDates.map((item) => {
      const timeRemaining = item.date.getTime() - currentDate.getTime();
      const days = Math.floor(timeRemaining / (1000 * 60 * 60 * 24));
      const hours = Math.floor(
        (timeRemaining % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
      );
      const minutes = Math.floor((timeRemaining % (1000 * 60 * 60)) / (1000 * 60));
      const seconds = Math.floor((timeRemaining % (1000 * 60)) / 1000);
      return (
        <div key={item.label}>
          <span className="text-red-500">{item.label}: </span>
          <span className="text-white">
            {days}d {hours}h {minutes}m {seconds}s
          </span>
        </div>
      );
    });
    setRemainingTime(remaining);
  }, [currentDate]);

  return (
    <div className="flex flex-col items-center justify-center h-screen bg-black">
      <div className="text-4xl font-bold tracking-widest px-[4rem]">
        {remainingTime}
      </div>
      <div className="text-white font-bold text-2xl py-[2rem] px-[8rem] mt-4">"{responseQuote}"</div>
      <button onClick={getNewQuote} className="bg-red-500 hover:bg-red-700 focus:outline-none focus:ring-4 focus:ring-red-300 active:bg-red-900 text-white text-xl font-medium py-2 px-4 rounded shadow-lg transition-colors duration-300">New Quote</button>
      <div className="text-white text-xl mt-4">
        <div class="relative">
          <input type="file" onChange={readCustomQuotes} class="opacity-0 absolute inset-0 w-full h-full" />
          <button class="bg-red-500 hover:bg-red-700 focus:outline-none focus:ring-4 focus:ring-red-300 active:bg-red-900 text-white text-xl font-medium py-2 px-4 rounded shadow-lg transition-colors duration-300">
            Choose File
          </button>
        </div>
        <button onClick={getRandomCustomQuote} className="text-white hover:text-red-500 text-xl duration-200 py-[2rem]">Random Quote</button>
      </div>
    </div>
  );
}

export default App;
