const { Configuration, OpenAIApi } = require("openai");

async function fetchQuote() {
  const configuration = new Configuration({
    apiKey: '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'
  });
  const openai = new OpenAIApi(configuration);

  const response = await openai.createCompletion({
    model: "text-davinci-003",
    prompt: "Barn likes to give sarcastic response, answer the below question as barn\n\nGive a random quote for IIT-JEE Aspirant preparing for JEE which can motivate him to continue studying Barn",
    temperature: 0.7,
    max_tokens: 256
  });
  return response.data.choices[0].text.match(/"([^"]*)"/)[1];;
  
}

module.exports = { fetchQuote };
